export default {
	data() {
		return {
			data: [],
		};
	},
	
	methods: {
		addCheater(){
			uni.showModal({
			    title: '提示',
			    content: '确定要向该案件添加骗子吗？',
			    success: function (res) {
			        if (res.confirm) {
			            //console.log('用户点击确定');
						
						uni.navigateTo({
							url: '/pages/cheaterAdd/cheaterAdd'
						})
						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
};
